:root {
  --primary-color: #FFBF0D;
  --secondary-color: #0DC5FF;
  --third-color: #4F4F4F;
  --footer-color: #000410;
}

body {
  font-family: "Tajawal", sans-serif;
}

header.site-header .site-search .widget_product_search form:before {
  display: none;
}
header.site-header .site-search .widget_product_search form input[type=search] {
  background-color: transparent;
  box-shadow: none;
  border: 2px solid;
  padding: 1em !important;
  position: relative;
  right: 152px;
  top: 25px;
  width: 376px;
  border-radius: 6px;
  border-color: var(--primary-color);
  transition: all 0.15s ease-in-out;
  letter-spacing: 0.06em;
}
header.site-header .site-search .widget_product_search form input[type=search]:focus {
  outline: none;
  border-width: medium;
  box-shadow: 0px 1px 5px 1px #5500ff75;
}
@media screen and (max-width: 975px) {
  header.site-header .site-search .widget_product_search form input[type=search] {
    right: 195px;
  }
}
header.site-header .widget_product_search form button[type=submit] {
  clip-path: unset;
  clip: unset;
  overflow: visible;
  color: #000;
  right: 0;
  left: auto;
  background-color: transparent;
  height: 100%;
  width: 25%;
  top: 25px;
  visibility: hidden;
}
@media screen and (max-width: 1030px) {
  header.site-header .widget_product_search form button[type=submit] {
    right: -30px;
  }
}
@media screen and (max-width: 975px) {
  header.site-header .widget_product_search form button[type=submit] {
    right: 4px;
  }
}
header.site-header .site-branding img {
  width: 140px;
  height: 105px;
}
@media screen and (max-width: 768px) {
  header.site-header .site-branding img {
    width: 100px;
    height: 75px;
  }
}

.site-header .custom-logo-link {
  outline: none;
}

.custom-logo-link {
  width: 140px;
}

.is-form-style {
  position: absolute;
  left: 22%;
  width: 51%;
  bottom: 42%;
}
@media screen and (max-width: 1030px) {
  .is-form-style {
    left: 12%;
    width: 63%;
    bottom: 54%;
  }
}
@media screen and (max-width: 768px) {
  .is-form-style {
    display: none;
  }
}

.is-form-style.is-form-style-3 input.is-search-input {
  border-right: 1px solid var(--primary-color) !important;
  padding-right: 22px;
}

.is-form-style input.is-search-input {
  height: 49px;
  border-radius: 46px;
  border: 1px solid var(--primary-color) !important;
  box-shadow: 0px 0px 4px 2px #00000017;
  transition: all 0.3s ease-in-out;
}

input[type=search]:focus {
  background: #fdfdfd;
  box-shadow: 0px 0px 11px 3px #ffbf0d63;
}

.is-search-icon {
  background: transparent;
  border: none;
  position: relative;
  left: 130%;
  top: -2px;
  pointer-events: all;
}
.is-search-icon:hover {
  background: transparent;
}
.is-search-icon svg {
  width: 30px;
  opacity: 0.6;
  transition: all 0.3s ease-in-out;
}
.is-search-icon svg:hover {
  opacity: 1;
}

#is-ajax-search-result-19 {
  width: 44% !important;
  left: 27% !important;
  box-shadow: 0px 0px 4px 2px #00000017;
}

#is-ajax-search-details-19 {
  display: none !important;
}

.is-ajax-search-result a {
  color: #946d02 !important;
}

.is-ajax-search-result .is-ajax-search-post:hover {
  background: #f7f7f7 !important;
}

.is-search-submit {
  pointer-events: none;
}

.is-ajax-search .is-loader-image {
  left: 40px !important;
  right: unset !important;
  width: 50px !important;
  height: 50px !important;
}

.site-header-cart .cart-contents:after {
  color: var(--primary-color);
}

#site-navigation li.current-menu-item a {
  color: var(--primary-color);
}
@media screen and (max-width: 768px) {
  #site-navigation li {
    text-align: center;
  }
}
#site-navigation li a {
  color: #fff;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width: 768px) {
  #site-navigation li a {
    color: #333;
    font-size: 1.5em;
  }
}
#site-navigation li a:hover {
  color: var(--primary-color);
}

.storefront-primary-navigation {
  background-color: #000;
}
@media screen and (max-width: 768px) {
  .storefront-primary-navigation {
    background-color: transparent;
  }
}

.site-header-cart .cart-contents .amount {
  color: #fff;
}

.site-header-cart .cart-contents .count {
  color: #fff;
}

h1.entry-title.loaded {
  display: none;
}

.entry-content.loaded p {
  font-size: 2rem;
  color: #fff;
}

.page-template-template-homepage.has-post-thumbnail .type-page.has-post-thumbnail {
  display: none !important;
}

.site-header {
  border: none;
}

.hentry .entry-header {
  display: none;
}

.page-template-template-homepage:not(.has-post-thumbnail) .site-main {
  padding-top: 0;
}

.page-template-template-homepage .type-page {
  padding-bottom: 0;
}

.hero {
  height: 450px;
  position: relative;
  top: -84px;
  background-size: cover;
  position: relative;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #0c0a00b5;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.hero__yellow {
  color: var(--primary-color);
}
.hero__content {
  position: relative;
  top: 15px;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.hero__content h1 {
  color: #fff;
  line-height: 1.4;
  font-weight: 600;
  font-size: 2.4em;
}
@media screen and (max-width: 877px) {
  .hero__content h1 {
    font-size: 2em;
  }
}
@media screen and (max-width: 768px) {
  .hero__content h1 {
    font-size: 1.3em;
    margin-left: 1em;
    margin-right: 1em;
  }
}
@media screen and (max-width: 768px) {
  .hero__content h1 {
    font-size: 1.1em;
  }
}
.hero__content h1 p {
  margin-bottom: 0.3em;
}
.hero__content-cta button {
  background: #fff;
  font-size: 1.6em;
  border-radius: 35px;
  padding: 7px 39px;
  transition: all 0.3s ease-in-out;
  border: 1px solid transparent;
}
@media screen and (max-width: 768px) {
  .hero__content-cta button {
    font-size: 1.3em;
    padding: 2px 27px;
  }
}
.hero__content-cta button:hover {
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.content-area {
  margin-bottom: 0;
}

.page-template-template-homepage .type-page {
  padding: 0;
}

.storefront-product-section .section-title {
  font-size: 2.5em;
  font-weight: 600;
  color: var(--third-color);
}
@media screen and (max-width: 768px) {
  .storefront-product-section .section-title {
    font-size: 2.2em;
  }
}

ul.products li.product .woocommerce-LoopProduct-link {
  box-shadow: 0px 0px 7px -2px #00000026;
  text-align: center;
}
ul.products li.product .woocommerce-LoopProduct-link.women img {
  border: 1px solid #f97d9145;
}
ul.products li.product .woocommerce-LoopProduct-link.men img {
  border: 1px solid #5500ff2e;
}
ul.products li.product .woocommerce-LoopProduct-link h2, ul.products li.product .woocommerce-LoopProduct-link span.price, ul.products li.product .woocommerce-LoopProduct-link span.short_description {
  padding-left: 0.8em;
  display: block;
  padding-right: 0.8em;
}
ul.products li.product .woocommerce-LoopProduct-link h2 {
  font-weight: 700;
}
ul.products li.product .woocommerce-LoopProduct-link span.price {
  padding-bottom: 1.1em;
  font-size: 1.3em;
  text-align: center;
}
ul.products li.product .woocommerce-LoopProduct-link span.price ins span.woocommerce-Price-amount.amount {
  color: #ff005e;
  font-weight: 700;
}

ul.products li.product .button {
  box-shadow: 0px 0px 3px 1px #00000026;
}

.yith-wcwl-icon.fa-heart-o:before,
.yith-wcwl-icon.fa-heart:before {
  background: #ffffff26;
  padding: 6px;
  border-radius: 14px;
  box-shadow: 0px 0px 5px 1px #00000021;
  padding-top: 6px;
  padding-bottom: 4px;
  filter: grayscale(0);
  color: var(--secondary-color);
}

.yith-wcwl-icon.fa-heart-o:before {
  color: #0000004a;
}

.onsale {
  margin: 6px auto;
  display: inline-block;
  width: 100%;
  text-align: center;
  border: #fff 1px solid;
  background: #ff0063de;
  color: #fff;
  font-size: 1em;
}

.single-product .onsale {
  width: 50%;
}
@media screen and (max-width: 767px) {
  .single-product .onsale {
    width: 100%;
  }
}

.single-product .woocommerce-LoopProduct-link.woocommerce-loop-product__link .onsale {
  width: 100%;
}

.marks {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 77px;
}
@media screen and (max-width: 768px) {
  .marks {
    grid-template-columns: 1fr;
  }
}
.marks__content {
  width: 330px;
  margin: auto;
  border: 1px solid var(--secondary-color);
  padding: 32px;
  border-radius: 6px;
  transition: all 0.5s ease-in;
}
.marks__content:hover {
  box-shadow: 0px 0px 25px -1px #0dc5ff59;
  transform: translateY(-4px);
}

.category {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 35px;
  margin-bottom: 3em;
}
@media screen and (max-width: 768px) {
  .category {
    grid-template-columns: 1fr;
  }
}
.category__card {
  height: 300px;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 2px;
  overflow: hidden;
}
.category__card::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #0c0a006e;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.category__card-content {
  position: relative;
  z-index: 2;
  text-align: center;
  top: 15px;
}
.category__card-content h2 {
  font-size: 2.5em;
  font-weight: 600;
  color: #fff;
  border-bottom: 3px solid var(--primary-color);
}
.category__card-content a button {
  background: #fff;
  font-size: 1.3em;
  border-radius: 35px;
  padding: 4px 25px;
  transition: all 0.3s ease-in-out;
  border: 1px solid transparent;
}
.category__card-content a button:hover {
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.discount {
  margin-bottom: 3em;
}
.discount__cover {
  height: 323px;
  background-position: center;
}

.site-footer {
  background-color: var(--footer-color);
}

.footer__link img {
  margin-bottom: 5em;
}
@media screen and (max-width: 768px) {
  .footer__link img {
    margin-bottom: 1em;
  }
}

.zoom-social-icons-list {
